.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.btnBox {
  display: flex;
}

.text {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 600;
  color: #3498db;
  width: 35px;
  height: 35px;
}
